@import url(https://fonts.googleapis.com/css?family=Sedgwick+Ave|Lobster|Allerta+Stencil);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, #app {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-family: 'Sedgwick Ave', serif;
  background: -webkit-radial-gradient(ellipse, #202060 10%, black 80%);
  background: radial-gradient(ellipse, #202060 10%, black 80%); }

#wrapper {
  height: 100%;
  cursor: default; }
  #wrapper > svg {
    width: 100%;
    margin: 10% auto;
    margin-bottom: 5%; }
  #wrapper > h3 {
    text-align: center;
    font-size: calc(1em + 3vmax);
    color: #ddd; }
  #wrapper > .pad-store {
    width: calc(15rem + 38vw);
    font-family: 'Allerta Stencil';
    display: flex;
    position: absolute;
    bottom: 3%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: 2%;
    border-top: 2px solid black; }
    @media only screen and (max-width: 800px) {
      #wrapper > .pad-store {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        grid-gap: 7px; } }
    @media only screen and (min-width: 700px) and (max-width: 800px) {
      #wrapper > .pad-store {
        bottom: 10px; } }
    #wrapper > .pad-store > .drum-pad {
      margin: 0.2%;
      width: 22%;
      height: 10%;
      background: black;
      text-align: center;
      padding: 0.5% 2%;
      border-radius: 5px;
      color: #ddd;
      cursor: pointer; }
      @media only screen and (max-width: 800px) {
        #wrapper > .pad-store > .drum-pad {
          width: 100%;
          height: 100%; } }
      @media only screen and (min-height: 1000px) {
        #wrapper > .pad-store > .drum-pad {
          padding: 2% 2% !important; } }
      @media only screen and (min-width: 700px) and (max-width: 800px) {
        #wrapper > .pad-store > .drum-pad {
          padding: 0 1% !important;
          height: 80%; } }
    @media only screen and (max-width: 800px) {
      #wrapper > .pad-store #div-C {
        grid-column: 2/3;
        margin-left: 50%; } }

.float {
  -webkit-animation: float 1s ease-in-out;
          animation: float 1s ease-in-out; }

@-webkit-keyframes float {
  0% {
    box-shadow: 0 5px 15px 0px rgba(255, 160, 0, 0.6);
    -webkit-transform: translatey(0px);
            transform: translatey(0px); }
  50% {
    box-shadow: 0 25px 15px 0px rgba(255, 160, 0, 0.2);
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px); }
  100% {
    box-shadow: 0 5px 15px 0px rgba(255, 160, 0, 0.6);
    -webkit-transform: translatey(0px);
            transform: translatey(0px); } }

@keyframes float {
  0% {
    box-shadow: 0 5px 15px 0px rgba(255, 160, 0, 0.6);
    -webkit-transform: translatey(0px);
            transform: translatey(0px); }
  50% {
    box-shadow: 0 25px 15px 0px rgba(255, 160, 0, 0.2);
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px); }
  100% {
    box-shadow: 0 5px 15px 0px rgba(255, 160, 0, 0.6);
    -webkit-transform: translatey(0px);
            transform: translatey(0px); } }

@media only screen and (max-width: 800px) and (min-height: 1000px) {
  body #wrapper h3 {
    font-size: calc(0.5rem + 6vw); } }

body {
  margin: 0;
  padding: 0; }

